import React from "react";
import * as RB from 'react-bootstrap';



const BatchNumberList = ({BnList, SnListF}) => {

    // console.log(BnList,"BNListttttttt")
    
    return (
        <>

            <RB.Table striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Item Code</th>
                        <th>Item Name</th>
                        <th>Batch No.</th>
                        <th>Lot No.</th>
                        <th>In Quantity</th>
                        <th>Batch Date</th>
                        <th>Mfg Date</th>
                        <th>Exp. Date</th>
                        <th>MRP</th>
                    </tr>
                </thead>
                <tbody>
                  
                    {
                        BnList && BnList.map((BN, index) => {
                            return (
                                <tr key={index}>
                                    <td>{++index}</td>
                                    <td onClick={() => SnListF(BN && BN.batch_id)} style={{cursor:'pointer'}}> {BN && BN.item_code}</td>
                                    <td>{BN.item_name}</td>
                                    <td>{BN.batch_number}</td>
                                    <td>{BN.lot_no}</td>
                                    <td>{BN.qty_in}</td>
                                    <td>{BN.batch_date}</td>
                                    <td>{BN.mfg_date}</td>
                                    <td>{BN.expiry_date}</td>
                                    <td>{BN.mrp}</td>
                                </tr>
                            )
                        })
                    }

                </tbody>
            </RB.Table>
        </>
    )
}
export default BatchNumberList;